import React from 'react';
import {RenderNews} from './news';
import { Button, ButtonGroup} from 'react-bootstrap';

function getUnixSec(){
    const dateTime = Date.now();
    const timestamp = Math.floor(dateTime / 1000);
    return timestamp
}

class NewsList extends React.Component{
    constructor(props){
        super(props)
        this.contractManager = props.cm
        this.chain_addr = props.addr
        this.canViewNewsState = []
        this.canViewNewsIdx = []
        this.newsObjs = [] // update by componentDidUpdate
        this.state = {loaded: false, n_total: 0, offset: 0, count: 0}
        this.num_each_page = 3
        this.last_page = this.last_page.bind(this);
        this.next_page = this.next_page.bind(this);
    }

    async componentDidMount(){
        // get n_total and update count and offset
        let n_news = await this.contractManager.getTopicCount()
        for(let i=n_news;i>=1;i--){
            let canView = await this.contractManager.permissionCheck(i)
            if(canView === 0 || canView === 3 || canView === 4){
                this.canViewNewsState.push(canView)
                this.canViewNewsIdx.push(i)
            }
        }
        let news_count = this.canViewNewsIdx.length
        let display_count = news_count > this.num_each_page? this.num_each_page : news_count;
        await this.fetch_news(0, display_count)
        this.setState({
            n_total: news_count,
            offset: 0,
            count: display_count,
            loaded: true,
        })
        // await this.fetch_news(this.state.offset, this.state.count)
        console.log(this.state)
    }

    async fetch_news(offset, count){
        let news_objs = []
        for(var cnt = 0; cnt < count; cnt++){
            let news_id = this.canViewNewsIdx[offset + cnt]
            let news_canView = this.canViewNewsState[offset + cnt]
            let news = await this.contractManager.getTopic(news_id, true)
            if(news_canView === 4 || getUnixSec() > news.expirationTime){
                news.expired = true;
            } else {
                news.expired = false;
            }
            news.news_id = news_id
            news_objs.push(news)
        }
        this.newsObjs = news_objs
    }

    async componentDidUpdate(){
        // fetch news
        console.log("did update")
        // await this.fetch_news(this.state.offset, this.state.count)
    }

    async next_page(){
        let new_offset = this.state.offset + this.num_each_page
        let new_count = this.state.n_total - new_offset
        if(new_count > this.num_each_page){
            new_count = this.num_each_page
        }
        console.log("new", new_offset, new_count)
        await this.fetch_news(new_offset, new_count)
        console.log(this.newsObjs)
        this.setState({
            offset: new_offset,
            count: new_count,
        })
    }

    async last_page(){
        let new_offset = this.state.offset - this.num_each_page
        let new_count = this.state.n_total - new_offset
        if(new_count > this.num_each_page){
            new_count = this.num_each_page
        }
        await this.fetch_news(new_offset, new_count)
        this.setState({
            offset: new_offset,
            count: new_count,
        })
    }

    getNewsBlock(title, content, news_id, category, op1, op2, user_addr, expired) {
        return (<RenderNews title={title} content={content} news_id={news_id} category={category} bt0={op1} bt1={op2} cm={this.contractManager} addr={user_addr} expired={expired} key={news_id} />);
    }

    list_news(){
        console.log("render", this.newsObjs)
        let news_jsx = []
        for(var i=0;i<this.newsObjs.length;i++){
            let newsObj = this.newsObjs[i]
            let newsDesc = newsObj.desc;
            let details = newsDesc.split(";")
            let title = details[0]
            let content = details[1]
            let category = details[2]
            let newsOpts = newsObj.options.split(";");
            let opt1 = newsOpts[0]
            let opt2 = newsOpts[1]
            let expired = newsObj.expired;
            let news_id = newsObj.news_id
            let newsBlock = this.getNewsBlock(title, content, news_id, category, opt1, opt2, this.chain_addr, expired)
            news_jsx.push(newsBlock)
        }
        var no_last_page = false, no_next_page = false
        if(this.state.offset + this.state.count >= this.state.n_total){
            no_next_page = true
        }
        if(this.state.offset === 0){
            no_last_page = true
        }
        return(
          <div id="news">
          <h2> News List </h2>
          {news_jsx}
          <ButtonGroup className="mr-2" aria-label="LastPage group">
          <Button size='lg' onClick={this.last_page} disabled={no_last_page}> {"Last Page"} </Button>
          </ButtonGroup>
          <ButtonGroup className="mr-2" aria-label="NextPage group">
          <Button size='lg' onClick={this.next_page} disabled={no_next_page}> {"Next Page"} </Button>
          </ButtonGroup>
        </div>
        )
    }

    render(){
        if(!this.state.loaded){
            return (
                <h2>loading news... </h2>
            )
        }
        return (
            this.list_news()
        )
    }
}

export {NewsList};